<template>
  <div>
    <el-table :data="tableData" style="width: 100%" v-loading="loading">
      <el-table-column width="50px" prop="index" label="序号" />
      <el-table-column
        align="center"
        header-align="center"
        prop="title"
        label="食谱名称"
      >
        <template slot-scope="scope">
          <div :title="scope.row.title">
            {{ scope.row.title }}
          </div>
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        header-align="center"
        width="200px"
        label="宝宝"
      >
        <template slot-scope="scope">
          <div style="text-align: center">
            <div>
              {{ scope.row.family_member && scope.row.family_member.name }}
            </div>
            <div>
              {{
                (scope.row.family_member && scope.row.family_member.babyId) ||
                  scope.row.family_member.id
              }}
            </div>
          </div>
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        header-align="center"
        width="200px"
        label="用户"
      >
        <template slot-scope="scope">
          <div>
            <div style="display: inline-block">
              <el-avatar
                :size="'small'"
                :src="scope.row && scope.row.user.avatar"
                style="vertical-align: middle"
              />
              {{
                scope.row.user.remark_name
                  ? scope.row.user.remark_name
                  : scope.row.user.nickname
              }}
              <svg-icon v-if="scope.row.is_replace" icon-class="replace" />
            </div>
            <div>
              {{ scope.row.user._id }}
            </div>
          </div>
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        header-align="center"
        prop="creater"
        label="创建人"
      >
        <template slot-scope="scope">
          {{ scope.row.creater.name }}
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        header-align="center"
        prop="sender"
        width="80px"
        label="发送人"
      >
        <template slot-scope="scope">
          {{ scope.row.sender ? scope.row.sender.name : '未知' }}
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        header-align="center"
        prop="createTime"
        width="220px"
        label="创建时间"
      >
        <template slot-scope="scope">
          {{ new Date(scope.row.createTime) | formatTime() }}
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        header-align="center"
        label="任务执行日期"
      >
        <template slot-scope="scope">
          {{ new Date(scope.row.executeTime) | formatTime('y-m-d') }}
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        header-align="center"
        width="220px"
        label="发送时间"
      >
        <template slot-scope="scope">
          <div>
            <span v-if="scope.row.status === 5">
              已关闭
            </span>
            <span v-else-if="taskHasNotSend(scope.row.status)">
              待发送
            </span>
            <span v-else>
              {{ new Date(scope.row.sendTime) | formatTime() }}
            </span>
          </div>
        </template>
      </el-table-column>
      <el-table-column align="center" header-align="center" label="完成状态">
        <template slot-scope="scope">
          {{ scope.row.status | recipesTaskStatus }}
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        header-align="center"
        prop="address"
        label="操作"
        ><template slot-scope="scope">
          <div class="flex-row-center">
            <el-button type="text" size="small" @click="handleClick(scope.row)">
              {{ switchHandleText(scope.row.status) }}
            </el-button>
            <el-button
              type="text"
              size="small"
              v-if="canBeCanceled(scope.row.status)"
              @click="handleClose(scope.row)"
              >关闭任务</el-button
            >
          </div>
        </template>
      </el-table-column>
    </el-table>
    <div style="margin-top: 10px;text-align: center;">
      <el-pagination
        background
        :current-page.sync="currentPage"
        :page-sizes="[10, 50, 100]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      />
    </div>
    <task-close-dialog
      @closeTask="closeTask"
      @closeDialog="closeDialogVisible = false"
      :closeDialogVisible="closeDialogVisible"
    ></task-close-dialog>
  </div>
</template>

<script>
import { getRecipesTaskList, closeTask } from '@/api/recipes'
import { mapMutations, mapActions, mapGetters } from 'vuex'
import { localSave } from '@/utils'
import TaskCloseDialog from '@/components/TaskCloseDialog'

export default {
  name: 'TaskList',
  props: {
    taskType: {
      type: Number,
      default: 1
    },
    searchForm: {
      type: Object,
      default: () => {}
    }
  },
  components: {
    TaskCloseDialog
  },
  data() {
    return {
      closeDialogVisible: false,
      closeTaskId: '',
      loading: false,
      currentPage: 1,
      pageSize: 10,
      total: 0,
      tableData: []
    }
  },
  computed: {
    ...mapGetters(['recipesBabies', 'selectBaby'])
  },
  mounted() {
    this.getAllTaskList({ taskStatus: 1 })
  },
  methods: {
    ...mapMutations({
      setSelectBaby: 'health/SET_SELECTBABY',
      setTaskCount: 'recipes/SET_TASKCOUNT',
      setCreaters: 'recipes/SET_CREATERS',
      setSenders: 'recipes/SET_SENDERS'
    }),
    ...mapActions({
      getBabyList: 'recipes/getBabyList'
    }),
    taskHasNotSend(status) {
      return status === 0 || status === 2
    },
    handleClose(task) {
      this.closeDialogVisible = true
      this.closeTaskId = task._id
    },
    closeTask(reasonForm) {
      // 请求取消任务的接口,取消成功后刷新任务列表
      const params = {
        reason: reasonForm.closeReasonType,
        reason_message: reasonForm.closeReason
      }
      closeTask(this.closeTaskId, params).then(res => {
        if (res.code === 20000) {
          this.closeDialogVisible = false
          this.$message.success('关闭任务成功')
          this.$emit('closeTask')
        }
      })
    },
    canBeCanceled(status) {
      return status === 0 || status === 2
    },
    switchHandleText(status) {
      switch (status) {
        case 0:
          return '去发送'
        case 1:
          return '去回复'
        case 2:
          return '去发送'
        case 3:
          return '查看详情'
        case 4:
          return '查看详情'
        case 5:
          return '查看详情'
      }
    },
    getAllTaskList(searchParams = {}) {
      this.loading = true
      const params = {
        ...searchParams,
        currentPage: this.currentPage,
        pageSize: this.pageSize
      }
      getRecipesTaskList(params).then(res => {
        if (res.code === 20000) {
          this.loading = false
          const data = res.data
          this.tableData = data.list
          this.total = data.count
          // 设置tab标签上的任务数量
          const taskCountObj = {
            todayListNum: data.todayListNum,
            allListNum: data.allListNum,
            needToRelayNum: data.needToRelayNum
          }
          this.setTaskCount(taskCountObj)
          this.setCreaters(data.creater)
          this.setSenders(data.sender)
        }
      })
    },
    handleSizeChange(val) {
      this.pageSize = val
      this.getAllTaskList(this.searchForm)
    },
    handleCurrentChange(val) {
      this.currentPage = val
      this.getAllTaskList(this.searchForm)
    },
    handleClick(row) {
      // 设置localStorage中的currentTask
      localSave('currentTask', JSON.stringify(row))
      switch (row.status) {
        case 0:
          this.$router.push({
            path: 'recipes/send',
            query: { id: row.family_member._id }
          })
          break
        case 1:
          this.$router.push({
            path: 'recipes/comment',
            query: { id: row.family_member._id }
          })
          break
        case 2:
          this.$router.push({
            path: 'recipes/send',
            query: { id: row.family_member._id }
          })
          break
        case 3:
          this.$router.push({
            path: 'recipes/detail',
            query: { id: row.family_member._id }
          })
          break
        case 4:
          this.$router.push({
            path: 'recipes/detail',
            query: { id: row.family_member._id }
          })
          break
        case 5:
          this.$router.push({
            path: 'recipes/close',
            query: { babyId: row.family_member._id, taskId: row._id }
          })
      }
    }
  }
}
</script>
