<template>
  <div class="guidance-container">
    <el-card style="margin-bottom: 30px;">
      <div class="express-container">
        <div class="express-item" @click="$router.push({ name: 'RecipesAdd' })">
          新建定制食谱任务
        </div>
      </div>
      <div class="search-container">
        <el-row>
          <el-col :span="6">
            <div class="search-item">
              <div>宝宝乳名</div>
              <div>
                <el-input
                  v-model="searchForm.name"
                  placeholder="请输入宝宝乳名"
                  size="mini"
                />
              </div>
            </div>
          </el-col>
          <el-col :span="6">
            <div class="search-item">
              <div>用户</div>
              <div>
                <el-input
                  v-model="searchForm.nickname"
                  placeholder="请输入用户昵称或备注名"
                  size="mini"
                />
              </div>
            </div>
          </el-col>
          <el-col :span="6">
            <div class="search-item">
              <div>用户ID</div>
              <div>
                <el-input
                  v-model="searchForm.uid"
                  placeholder="请输入用户ID"
                  size="mini"
                />
              </div>
            </div>
          </el-col>
          <el-col :span="6">
            <div class="search-item">
              <div>完成状态</div>
              <div>
                <el-select
                  v-model="searchForm.status"
                  size="mini"
                  :disabled="statusDisabled"
                  placeholder="请选择"
                >
                  <el-option
                    v-for="item in statusOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  />
                </el-select>
              </div>
            </div>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="6">
            <div class="search-item">
              <div>创建人</div>
              <div>
                <el-select v-model="searchForm.createrId" size="mini">
                  <el-option
                    v-for="item in creaters"
                    :key="item._id"
                    :label="item.name"
                    :value="item._id"
                  />
                </el-select>
              </div>
            </div>
          </el-col>
          <el-col :span="6">
            <div class="search-item">
              <div>发送人</div>
              <div>
                <el-select v-model="searchForm.senderId" size="mini">
                  <el-option
                    v-for="item in senders"
                    :key="item._id"
                    :label="item.name"
                    :value="item._id"
                  />
                </el-select>
              </div>
            </div>
          </el-col>
          <el-col :span="6">
            <div class="search-item">
              <div>宝宝ID</div>
              <div>
                <el-input
                  v-model="searchForm.babyId"
                  placeholder="请输入宝宝ID"
                  size="mini"
                />
              </div>
            </div>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="6">
            <div class="search-time search-date-picker">
              <div>
                任务执行时间
              </div>
              <div>
                <el-date-picker
                  v-model="searchTime"
                  size="mini"
                  type="datetimerange"
                  range-separator="-"
                  start-placeholder="请选择时间范围起始"
                  end-placeholder="请选择时间范围结束"
                />
              </div>
            </div>
          </el-col>
          <el-col :span="16">
            <div class="recipes-button">
              <div>
                <el-button type="primary" size="mini" @click="search"
                  >搜索</el-button
                >
              </div>
              <div>
                <el-button size="mini" @click="reset">重置</el-button>
              </div>
            </div>
          </el-col>
        </el-row>
      </div>
    </el-card>
    <el-card>
      <el-tabs
        @tab-click="handleTab"
        v-model="activeName"
        class="tab-container"
        style="padding-right: 20px"
      >
        <el-tab-pane name="1">
          <span slot="label">
            <div>
              <el-badge
                v-if="recipesTaskCount.todayListNum > 0"
                :value="recipesTaskCount.todayListNum"
                class="item"
              >
                今日待发送
              </el-badge>
              <span v-else>今日待发送</span>
            </div>
          </span>
        </el-tab-pane>
        <el-tab-pane name="2">
          <span slot="label">
            <div>
              <el-badge
                v-if="recipesTaskCount.allListNum > 0"
                :value="recipesTaskCount.allListNum"
                class="item"
              >
                全部待发送
              </el-badge>
              <span v-else>全部待发送</span>
            </div>
          </span>
        </el-tab-pane>
        <el-tab-pane name="3">
          <span slot="label">
            <div>
              <el-badge
                v-if="recipesTaskCount.needToRelayNum > 0"
                :value="recipesTaskCount.needToRelayNum"
                class="item"
              >
                留言待回复
              </el-badge>
              <span v-else>留言待回复</span>
            </div>
          </span>
        </el-tab-pane>
        <el-tab-pane name="0">
          <span slot="label">
            全部任务
          </span>
        </el-tab-pane>
      </el-tabs>
      <div class="table-title">
        定制食谱任务列表
      </div>
      <task-list ref="taskList" :search-form="searchForm" @closeTask="search" />
    </el-card>
  </div>
</template>

<script>
import TaskList from './components/TaskList'
import { mapGetters } from 'vuex'

export default {
  name: 'Guidance',
  components: {
    TaskList
  },
  data() {
    return {
      statusDisabled: true,
      searchForm: {
        name: '',
        nickname: '',
        uid: '',
        status: '',
        beginTime: '',
        endTime: '',
        createrId: '',
        senderId: '',
        taskStatus: '',
        babyId: ''
      },
      searchTime: null,
      activeName: '1',
      statusOptions: [
        {
          value: '',
          label: '今日待发送'
        }
      ]
    }
  },
  computed: {
    ...mapGetters(['recipesTaskCount', 'creaters', 'senders'])
  },
  methods: {
    resetSearchForm() {
      this.searchForm = {
        name: '',
        nickname: '',
        uid: '',
        status: '',
        beginTime: '',
        endTime: '',
        createrId: '',
        senderId: ''
      }
    },
    handleTab() {
      this.$refs.taskList.pageSize = 10
      this.$refs.taskList.currentPage = 1
      this.resetSearchForm()
      this.searchForm.taskStatus = this.activeName
      this.$refs.taskList.getAllTaskList(this.searchForm)
      if (this.activeName !== '0') {
        this.statusDisabled = true
        // 过滤完成状态的搜索条件
        this.filterSatatusOptions(this.activeName)
        return
      }
      this.statusDisabled = false
      this.resetStatus()
    },
    // 重置完成状态
    resetStatus() {
      this.searchForm.status = null
      this.statusOptions = [
        {
          value: 0,
          label: '待填写'
        },
        {
          value: 1,
          label: '留言待回复'
        },
        {
          value: 2,
          label: '已保存'
        },
        {
          value: 3,
          label: '已发送'
        },
        {
          value: 4,
          label: '已完成'
        },
        {
          value: 5,
          label: '已关闭'
        }
      ]
    },
    // 过滤完成状态
    filterSatatusOptions(activeName) {
      switch (activeName) {
        case '1':
          this.searchForm.status = ''
          this.statusOptions = [
            {
              value: '',
              label: '今日待发送'
            }
          ]
          break
        case '2':
          this.searchForm.status = ''
          this.statusOptions = [
            {
              value: '',
              label: '全部待发送'
            }
          ]
          break
        case '3':
          this.searchForm.status = ''
          this.statusOptions = [
            {
              value: '',
              label: '留言待回复'
            }
          ]
      }
    },
    // 根据搜索条件查询任务列表
    search() {
      this.$refs.taskList.pageSize = 10
      this.$refs.taskList.currentPage = 1
      this.searchForm.beginTime = this.searchTime ? this.searchTime[0] : ''
      this.searchForm.endTime = this.searchTime ? this.searchTime[1] : ''
      this.$refs.taskList.getAllTaskList(this.searchForm)
    },
    // 重置查询条件
    reset() {
      this.resetSearchForm()
      this.searchForm.taskStatus = this.activeName
      this.searchTime = null
      this.$refs.taskList.pageSize = 10
      this.$refs.taskList.currentPage = 1
      this.$refs.taskList.getAllTaskList(this.searchForm)
    },
    needKeepAlive(path) {
      const keepAliveRoutes = [
        'recipes/send',
        'recipes/detail',
        'recipes/comment',
        'recipes/add',
        'recipes/close'
      ]
      return keepAliveRoutes.some(route => path.includes(route))
    },
    refreshPage() {
      this.statusDisabled = true
      this.activeName = '1'
      this.statusOptions = [
        {
          value: '',
          label: '今日待发送'
        }
      ]
      this.reset()
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      if (!vm.needKeepAlive(from.path)) {
        vm.refreshPage()
      } else {
        vm.search()
      }
    })
  }
}
</script>
<style lang="scss">
.search-item {
  .el-input {
    width: 180px;
  }
}
.tab-container {
  .el-badge__content.is-fixed {
    top: 10px;
  }
}
</style>

<style scoped lang="scss">
@import '@/styles/healthManagement/page.scss';
.recipes-button {
  display: flex;
  justify-content: flex-end;
  margin-right: 60px;
  div {
    margin-right: 10px;
  }
}
.guidance-container {
  .search-container {
    height: 150px;
  }
}
</style>
